body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0B0C10;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.action-text-container {
  width: 80%;
  margin: 0 auto 0 auto;
}
.action-text {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  top: 30%;
  color: #66FCF1;
  font-size: 4em;
  -webkit-transition: font-size 0.2s; /* For Safari 3.1 to 6.0 */
  transition: font-size 0.2s;
  
  /* font-family: 'Pacifico', cursive; */
  font-family: 'Lobster', cursive;
  
}
.action-text:hover {
  font-size: 4.3em;
  /* width: 100%; */
}

@media only screen and (max-width: 768px) {
  .action-text {
    font-size: 30px;
  }

  .action-text:hover {
    font-size: 33px;
  }
}

.comment-text {
  color: #C5C6C7;
  font-family: 'Lobster', cursive;
  font-size: 25px;
  right: 15%;
  bottom: -50px;
  position: fixed;
  visibility: hidden;
}

.comment-text.animate {
  -webkit-animation: up-down 2s;
          animation: up-down 2s;
}

.comment-text-container {
  overflow: hidden;
}

@-webkit-keyframes up-down {
  0% { bottom: -50px; }
  10% { bottom: 20px; visibility: visible }
  90% { bottom: 20px; }
  100% { bottom: -50px; }
}

@keyframes up-down {
  0% { bottom: -50px; }
  10% { bottom: 20px; visibility: visible }
  90% { bottom: 20px; }
  100% { bottom: -50px; }
}

.comment-text.up {
  bottom: 30px
}

@media only screen and (max-width: 768px) {
  .comment-text {
    font-size: 19px;
  }
}
.game-button-span {
  font-family: 'Lobster', cursive;
  font-size: 2em;
  color: #C5C6C7;
  -webkit-transition: font-size 0.2s; /* For Safari 3.1 to 6.0 */
  transition: font-size 0.2s;
}

.game-button-span:hover {
  font-size: 2.3em;
}

.off {
  opacity: 0.4;
  filter: alpha(opacity=50); /* For IE8 and earlier */
}


@media only screen and (max-width: 768px) {
  .game-button-span {
    font-size: 18px;
  }

  .game-button-span:hover {
    font-size: 20px;
  }
}

.config-button {
  font-family: 'Lobster', cursive;
  font-size: 1.5em;
  color: #C5C6C7;
  position: fixed;
  top: 15px;
  left: 15px;
}

.config-button.unflipped {
  /* transform: rotate(180deg); */
  -webkit-animation: 0.2s unflip linear;
          animation: 0.2s unflip linear;
}

.config-button.flipped {
  /* transform: rotate(0deg); */
  -webkit-animation: 0.2s flip linear;
          animation: 0.2s flip linear;
}

@-webkit-keyframes flip {
  0%   { -webkit-transform: rotate(0deg); }
  15%   { -webkit-transform: rotate(-20deg); }
  20%   { -webkit-transform: rotate(-25deg); }
  80%  { -webkit-transform: rotate(205deg); }
  85%   { -webkit-transform: rotate(200deg); }
  100% { -webkit-transform: rotate(180deg); }
}

@keyframes flip {
  0%   { -webkit-transform: rotate(0deg); }
  15%   { -webkit-transform: rotate(-20deg); }
  20%   { -webkit-transform: rotate(-25deg); }
  80%  { -webkit-transform: rotate(205deg); }
  85%   { -webkit-transform: rotate(200deg); }
  100% { -webkit-transform: rotate(180deg); }
} 
/* 
@keyframes flip {
  0% {-webkit-transform: rotate(0deg);}
  100% {-webkit-transform: rotate(180deg);}
}

@keyframes unflip {
  0% {-webkit-transform: rotate(180deg);}
  100% {-webkit-transform: rotate(0deg);}
} */


@-webkit-keyframes unflip {
  0%   { -webkit-transform: rotate(180deg); }
  15%   { -webkit-transform: rotate(200deg); }
  20%   { -webkit-transform: rotate(205deg); }
  80%  { -webkit-transform: rotate(-25deg); }
  85%   { -webkit-transform: rotate(-20deg); }
  100% { -webkit-transform: rotate(0deg); }
}


@keyframes unflip {
  0%   { -webkit-transform: rotate(180deg); }
  15%   { -webkit-transform: rotate(200deg); }
  20%   { -webkit-transform: rotate(205deg); }
  80%  { -webkit-transform: rotate(-25deg); }
  85%   { -webkit-transform: rotate(-20deg); }
  100% { -webkit-transform: rotate(0deg); }
}

.button-container {
  top:-200px;
  visibility: hidden;
  position: absolute;
  width: 80%;
  margin: auto;
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  grid-template-rows: 5em;
  margin-bottom: 4em;
  transition: all 0.5s;
  -webkit-transition: all 0.5s; /* Safari */
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
}

.button-container.open {
  visibility: visible;
  top: 10%;
}

@media only screen and (max-width: 768px) {
  .config-button {
    font-size: 15px;
  }
}
/* Color pallette from this beautiful configuration */
/* https://blog.visme.co/wp-content/uploads/2016/09/website12-1024x512.jpg */

.App {
  text-align: center; 
  margin-top: 5em;
  overflow: auto;
}

.event-span {
  position: absolute;
  bottom:20%;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  font-family: 'Lobster', cursive;
  font-size: 25px;
  color: #45A29E;
  -webkit-animation: pulse 1s infinite;
  animation: pulse 1s infinite;
}
.event-span.lower {
  position: absolute;
  bottom:15%;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  font-family: 'Lobster', cursive;
  font-size: 25px;
  color: #45A29E;
  -webkit-animation: pulse 1s infinite;
  animation: pulse 1s infinite;
}

@-webkit-keyframes pulse {
  0% { font-size: 25px }
  50% { font-size: 28px }
  100% { font-size: 25px }
}

@keyframes pulse {
  0% { font-size: 25px }
  50% { font-size: 28px }
  100% { font-size: 25px }
}

